import { Upload, Icon, message } from 'antd'
import upload from '../utils/upload'
import '../styles/vov-dragger.scss'
import ReactDOM from 'react-dom'
import React from 'react'

const { Dragger } = Upload

export default class VovDragger extends React.Component {
  customRequest = async ({ file }) => {
    this.props.setPictures('https://www.baidu.com/img/bd_logo1.png')
    const url = await upload(file)
    this.props.setPictures(url)
  }

  render () {
    return (
      <Dragger
        customRequest={this.customRequest}
        className={`${this.props.className} vov-dragger`}
        listType="picture"
        accept="image/gif, image/jpeg, image/jpg, image/png">
          <p className="ant-upload-drag-icon">
            <Icon type="inbox" />
          </p>
          <p className="ant-upload-text">Click or drag image to this area to upload</p>
          <p className="ant-upload-hint">Support for a single or bulk upload. Strictly prohibit from uploading company data or other band files</p>
      </Dragger>
    )
  }
}


